<template>
  <div class="game">
    <div class="leftCol">
      <h3>Команды</h3>
      <input type="text" v-model="newTeam.nickname" placeholder="Название" class="textbox_teams">
      <input type="text" v-model="newTeam.balance" placeholder="Баланс" class="textbox_teams"><br/>
      <input type="button" value="Создать команду" @click="teamAdder(newTeam)" class="game_button_add">
      <input type="button" value="Очистить команды" @click="clearTeams()" class="game_button_delete">
      <div v-for="team in teams" :key="team.nickname">
        <router-link to="/team1" class="team_link"><h4>{{team.nickname}}</h4></router-link>
        <div class="team">
        Баланс:{{team.balance}}<br/>
        Ресурсы:<br/>
        ResourceName: Count<br/>
        <input type="button" value="Присоединиться к команде" class="game_button_add">
        </div>
      </div>
    </div>
    <div class="midCol">
      <input type="button" value="Сохранить игру" class="game_button_add" @click="saveGame()">
      <input type="button" value="Сохранить настройки" class="game_button_save" @click="saveTable()">
      <input type="button" value="Удалить настройки" class="game_button_delete" @click="clearRefTable()">
      <h1>Раунд {{count}}</h1>
      <input type="button" value="Следующий раунд" @click="nextRound()" class="game_button_add">
      <h3>Города ("Эталон")</h3>
      <div v-for="table in tables" :key="table">
      <input type="button" value="Добавить колонку" @click="columns.push('')" class="game_button_add">
      <input type="button" value="Добавить строку" @click="rows.push('')" class="game_button_add"><br/>
      <input type="text" placeholder="Объем рынка">
      <table  border="1" id='ref_table'>
        <tbody v-if="ref_table_1.length==0">          
        <tr v-for="row in rows" :key="row">
          <td v-for="column in columns" :key="column"><input type="text"></td>
        </tr>
        </tbody>
        <tbody v-else>
        <tr v-for="(row, row_i) in ref_table_1" :key="row_i">
          <td class="table_cell" v-for="(item, item_i) in row" :key="item_i"><span >{{item}}</span></td>
        </tr>
        </tbody>
      </table>
      </div>
    </div>
    <div class="rightCol">
      <ul>
        <router-link to="/game" class="nav_src"><li><img src="@/assets/loc.png" alt="" class="navImg"><span class="tooltip_text">Города</span></li><br/></router-link>
        <router-link to="/materials" class="nav_src"><li><img src="@/assets/gear.png" alt="" class="navImg"><span class="tooltip_text">Материалы</span></li><br/></router-link>
      </ul>
    </div>
  </div>
</template>
<script>
import { mapState,mapActions } from 'vuex'
import axios from 'axios'
export default {
  computed:mapState({
    teams: state => state.team.teams,
    ref_table_1: state => state.settings.ref_table_1,
    materials:state => state.settings.materials
  }),
    data(){
    return{
    count:1,
    banks:false,
    newTeam:{nickname:'', balance:''},
    rows:[{}],
    columns:[],
    tables:[{}],
    gameState:{
      reference_table:[],
      materials_tables:[]
    }
    }
  },
  methods:{
    ...mapActions('team',['addTeam']),
    ...mapActions('team',['clearTeams']),
    ...mapActions('settings',['addRefTable']),
    ...mapActions('settings',['clearRefTable']),
    teamAdder(team){
      this.addTeam(team)
      this.newTeam={}
    },
    nextRound(){
      this.count++
      this.tables.push('')
      this.banks=true
    },
    saveTable(){
      var table = document.getElementById('ref_table')
      var row=[]
      var rows = table.rows.length
      var columns = table.rows[0].cells.length
      for(var i=0;i<rows;i++){
        for(var j=0;j<columns;j++){
          row.push(table.rows[i].cells[j].children[0].value)
        }
        this.addRefTable(row)
        row=[]       
      }
    },
    saveGame(){
      this.gameState.materials_tables=this.materials
      this.gameState.reference_table = this.ref_table_1
      axios.post('https://optimizators.herokuapp.com/game/create',{data:this.gameState}).then(function (response) {
        console.log(response);
      })
      .catch(function (error) {
        console.log(error);
      })
    }
  }
}
</script>
<style>
table{
  margin: auto;
  width: auto;
}
.table_cell{
  width: 150px;
}
.nav_src{
  position: relative;
  display: inline-block;
}
.nav_src .tooltip_text {
  visibility: hidden;
  width: 120px;
  background-color: black;
  color: #fff;
  text-align: center;
  padding: 5px 0;
  border-radius: 6px;
  position: absolute;
  z-index: 1;
  top: 5px;
  right: 80%;
}
.navImg:hover ~ .tooltip_text {
  visibility: visible;
}
.textbox_teams{
  width: 85px;
  margin-bottom: 5px;
}
.game_button_add{
  border-radius: 5px;
  text-decoration: none;
  display: inline-block;
  padding: 5px 10px;
  background-image: linear-gradient(45deg, #6ab1d7 0%, #33d9de 50%, #002878 100%);
  background-position: 100% 0;
  background-size: 200% 200%;
  color: white;
  box-shadow: 0 16px 32px 0 rgba(0, 40, 120, .35);
  transition: .5s;
  margin:2px;
}
.game_button_delete{
  border-radius: 5px;
  text-decoration: none;
  display: inline-block;
  padding: 5px 10px;
  background-image: linear-gradient(45deg, white 0%, red 50%, black 100%);
  background-position: 100% 0;
  background-size: 200% 200%;
  color: white;
  box-shadow: 0 16px 32px 0 rgba(0, 40, 120, .35);
  transition: .5s;
  margin:2px;
}
.game_button_save{
  border-radius: 5px;
  text-decoration: none;
  display: inline-block;
  padding: 5px 10px;
  background-image: linear-gradient(45deg, rgb(94, 243, 106) 0%, rgb(33, 219, 16) 50%, rgb(6, 114, 11) 100%);
  background-position: 100% 0;
  background-size: 200% 200%;
  color: white;
  box-shadow: 0 16px 32px 0 rgba(0, 40, 120, .35);
  transition: .5s;
  margin:2px;
}
.game_button_add:hover, .game_button_delete:hover, .game_button_save:hover{
  box-shadow: 0 0 0 0 rgba(0, 40, 120, 0);
  background-position: 0 0;
}
ul{
  text-align: left;
}
.game{
  display: flex;
  height: 90vh;
}
.leftCol{
  width: 20%;
  padding: 5px;
}
.rightCol{
  width: 5%;
}
.leftCol, .rightCol, .midCol{
  margin-top: 10px;
  padding: 5px;
}
.midCol{
  width: 75%;
  border-right: 1px;
  border-top: 0px;
  border-bottom: 0px;
  border-left: 1px;
  border-color: blue;
  border-style: solid;
  border-radius: 1px;
  padding: 5px;
}

.navImg{
  width: 50px;
  height: 50px;
}
.team{
  border-style: solid;
  border-radius: 5px;
  border-width: 3px;
}
</style>